import "./styles/nav.css"
import './styles/App.css';
import "./styles/Header.css"
import "./styles/login.css"
import "./styles/userPage.css"
import "./styles/upload.css"
import "./styles/music.css"
import "./styles/audio.css"
import "./styles/search.css"

import EditProfile from "./component/editProfile"
import Header from "./component/header"
import AudioPlayer from "./component/audioplayer"
import Nav from "./component/nav"
import firebase from "firebase"
import About from "./component/about"
import Home from "./component/home"
import Login from "./component/login"
import { Route, BrowserRouter as Router, Switch } from 'react-router-dom';
import "./component/firebase";
import Signup from "./component/Signup"
//import auth from "firebase/auth";
import Upload from "./component/Upload"
import Music from "./component/music"
import Result from "./component/result"
import {  useEffect, useState } from "react";
import {uidContext} from "./context/uid"
import UserLikes from "./component/pages/userLikes"
import Listen from "./component/pages/listen"
import {Song, Playlist} from "./component/classes/song"
import {songContext, playlistContext} from "./context/songContext"
const audio = new Audio()
const song = new Song();


const playList = new Playlist()


function useForceUpdate(){
  const [value, setValue] = useState(0); // integer state
  return () => setValue(value => value + 1); // update the state to force render
}

function App() {
  let name1;
  const forceUpdate = useForceUpdate()
  const [progVal, setProgVal] = useState("0%");
  const [AudioPreview, setAudioPreview] = useState()
  const [uid, setUid] = useState(null)
  const[reRender, setReRender] = useState()
  firebase.auth().onAuthStateChanged(user=>setUid(user.uid))
  const db = firebase.database()
  let duration = 0;

  function volumeHandler(volume){
    audio.volume = volume
  }


  
  function play(src) {
    forceUpdate()
    if(song.audio.src === src.src.aduioURL){
        song.play()
        name1 = src.title
    }else{
        song.playSong(src)
        name1 = src.info.songID||src.info.title
    }
  
   
    if(audio){
        audio.addEventListener("timeupdate", ()=>{
            if(name1){
                let currentTime = audio.currentTime
                //only executes if a user has listened the song for atleast 30sec
                if (currentTime > 30 && currentTime < 30.2) {
                    console.log(3000)
                    let views;
                    let totalViews;
                    db.ref("users/" + src.info.userID + "/public/" + "songs/" + name1 +"/stats").once("value").then((snapshot) => {
                        views = snapshot.val().views;
                        totalViews = views + 1
                    }).then(() => {
                        db.ref("users/" + src.info.userID + "/public/" + "songs/" + name1+ "/stats").update({
                            "views": totalViews
                        }).then(() => {
                        db.ref("public/songs/" + name1).update({
                                "views": totalViews
                            })
                        })
                    })
                }
            }
        })   
    }
}

function stop(event) {
  forceUpdate()
  song.stop()
}

function playAudio(){
  forceUpdate()
  song.play()
}

function next(){
  forceUpdate()
  console.log(song.getNextSong())
  const nextSong = song.getNextSong()
  if(nextSong)
  {
    song.playSong(nextSong)
  }
}

function previous(){
  forceUpdate()
  const previousSong = song.getPreviousSong()
  if(previousSong)
  {
    song.playSong(previousSong)
  }
}



  return (
    <>
      <Router>
        <Switch> 
        
            <Route path="/login" exact component = {Login} />
            <Route path = "/Signup" exact component = {Signup} />
              <div className = "blue" id = "main-render">  
                <uidContext.Provider value = {uid}>
                    <songContext.Provider value = {song}>
                      <Route exact path="/about" render = {(props)=>(
                          <About {...props} />
                        )} />
                        <Route path="/upload" component={Upload} />
                        <Route exact path = "/account/edit"  component = {EditProfile} />

                        <Route path="/u/:uID" render = {(props)=>(
                          <Music {...props} play = {play} stop = {stop} audio = {audio}/>
                        )} />

                        <Route exact path="/" exact render ={(props)=>(
                          <Home {...props} play = {play} stop = {stop} audio = {audio}/>
                        )} />

                        <Route path = "/results"exact render = {(props) =>(
                          <Result {...props} play = {play} stop = {stop} audio = {audio}/>
                        )}/>

                         <Route path = "/listen"exact render = {(props) =>(
                          <Listen {...props} play = {play} stop = {stop} audio = {audio}/>
                        )}/>

                         <Route path = "/liked" exact render = {(props)=>(
                            <UserLikes {...props} play = {play} stop = {stop} audio = {audio} />
                          )} />
                          <Nav />
                        <Header />

                        <AudioPlayer 
                        progVal={progVal} 
                        stop={stop} 
                        playAudio={playAudio} 
                        audio = {audio} 
                        volumeHandler = {volumeHandler} 
                        AudioInfo = {AudioPreview}
                        next = {next}
                        previous = {previous}
                        />
                        
                  </songContext.Provider>
                </uidContext.Provider>
                 
              </div>
        </Switch>
      </Router>
    </>
  );
  
}
//#2CC8E5
export default App;

