import React from "react";
import firebase from "firebase"
import NavDiv1 from "./nav-comp/navDiv1"
import NavDiv2 from "./nav-comp/navDiv2"
import { useHistory } from 'react-router-dom';
import {MoosaxAuth} from "./functions/moosax-auth.js"

function Nav() {
    const auth = new MoosaxAuth()
    let user1;
    firebase.auth().onAuthStateChanged((user) => {
        user1 = user
    })
    return (
        <div id="nav">
            <nav>
                <div className = "nav-heading">
                    <h2>Moosax</h2>
                </div>
                    <div className = "main-nav">
                        <NavDiv1 />
                        <NavDiv2 />
                    <div className = "line" id = "nav-line"/>
                </div>
                <div className="signout" onClick={()=>auth.signOut()}>
                        <span className="material-icons">
                            login
                        </span>
                        <p>signout</p>
                </div>
            </nav>
        </div>
    )
}
export default Nav